body {
    background: #fffbf9;
}

.background-image {
    background-image: url(https://i.ibb.co/K2sTq7C/home-page-background.png);
    background-size: cover;
    height: 370px;
}
.lodingImghome {
    margin-left: auto;
    margin-right: auto;
}
.main-logo {
    text-align: center;
}
.main-logo img {
    border: 1px solid #fff;
    border-radius: 50%;
    background-color: #fff;
    margin-top: 50px;
    padding: 0px 15px;
}

.main-heading {
    margin-top: 65px;
    font-weight: 600;
    color: #fff;
    font-family: Poppins;
    font-size: 32px;
    text-align: center;
}

.quickSearch {
    margin-top: 20px;
    text-align: center;
}

.quickSearchHeading {
    font-family: Poppins;
    font-size: 30px;
    font-weight: bold;
    color: darkslateblue;
    margin-top: 33px;
}

.subHeading {
    color: #8c96ab;
    font-family: Poppins;
    font-size: 17px;
    line-height: 1;
    margin-bottom: 60px;
}
.tilecontainer {
    height: 150px;
    margin:10px 10px 10px 0;
    border-radius: 5px;
    box-shadow: 6px 6px 10px #d47440;
    transition: transform 0.5s, background 1s,color 0.7s;
}
.tilecontainer:hover{
    transform: scale(1.05,1.05);
    background: rgb(226, 223, 223);
    color:  #d47440;
}
.tileComponent1 {
    margin-left: -15px;
    float: left;
    width: 50%;
}

.tileComponent1 img {
   height: 150px;
    width: auto;
}

.tileComponent2 {
    padding: 10px 0 0 10px;
    float: left;
    width: 50%;
    height: 150px;
    border-left: 1px solid #000;
}

.componentHeading {
    font-size: 25px;
    font-weight: 600;
}

.componentSubHeading {
    font-size: 15px;
    font-family: poppins;
}
@media screen and (min-width:270px){
    .main-heading {
        font-size: 20px;
        margin-top: 10px;
    }
    .main-logo img {
        margin-top: 30px;
        height: 70px;
        width: 70px;
    }
    .tilecontainer{
      width: 100%;
    
    }
    .componentHeading{
        font-size: 18px;
    }
    .componentSubHeading{
        font-size: 14px;
    }
}
@media screen and (min-width:768px){
    .componentSubHeading{
        font-size: 14px;
    }
    .tilecontainer{
        width: 42%;
        margin-right: 20px;
        margin-left: 15px;
    }
    .tiles{
        margin-left: 5%;
    }
}
@media screen and (min-width:1000px)
{
    .tilecontainer{
        width:30%;
        margin:10px 20px 10px 0;
    }
    .main-logo img {
        margin-top: 50px;
        height: auto;
        width: auto;
    }
    .main-heading {
        font-size: 32px;
    }
}