.image img {
    width: 100%;
    height: 400px;
}

.para {
    margin-left: 65%;
    font-size: 20px;
    margin-top: -43px;
    font-weight: 600;
    padding: 8px 8px;
    width: fit-content;
    background-color: rgba(221, 213, 213, 0.5);
}

.tabs {
    display: flex;
    list-style: none;
    cursor: pointer;
    font-size: 20px;
    margin-left: -40px;
}

.overviewtab {
    margin-right: 20px;
}

.headingDeatail {
    font-weight: 700;
    font-family: poppins;
    color: #192f60;
    font-size: 30px;
}

.hr {
    height: 2px;
    margin-top: -2px;
    background: #ce0505;
    width: 92px;
    margin-left: 0;
}

.hr2 {
    height: 2px;
    margin-left: 110px;
    margin-top: -2px;
    background: #ce0505;
    width: 85px;
}

.bodyhr {
    height: 2px;
    background: #d2d9e6;
    margin-top: 10px;
}

@media screen and (max-width:720px) {
    .para {
        font-size: 12px;
        margin-left: 50%;
        margin-top: -48px;
    }
    .contentBody {
        padding-top: 20px;
        margin: 0;
    }
    .image img {
        height: 250px;
    }
}