*{
    font-family: poppins;
    
}
.secondcontainer{
    /* border: 1px solid black; */
box-shadow: 0px 3px 10px rgb(128, 128, 128);
margin: 3px 15px 15px 5px;
padding: 5px 0 10px 0;
border-radius: 17px;

}
.secondpage{
    overflow-y:scroll;
    height: 560px;
}
.restName{
    font-size: 28px;
    font-weight: bold;
    color: black;
    transition: font-size 1s;

}
.restName:hover{
    font-size: 32px;
    color: chocolate;
    text-decoration: none;
}
.card{
    display: flex;
}
.hr3{
    margin:15px auto 0 auto;
    height: 3px;
    width: 95%;
    background-color: #d5d5d5;

}
.listingimage img{
    border-radius: 15px;
    height: 13em;
    width: 13em;
}
.listingimage{
    padding:15px;
}
.heading{
    font-size: 25px;
    font-weight: bold;
}
.restdetail{
    padding: 20px 0 0 15px;
    font-weight: 500;
    font-family: poppins;
}

.restprice{
    font-size: 15px;
    padding:8px 0 5px 25px;
    font-weight: bold;
}
@media screen and (max-width:800px){
    .listingimage img{
        height: 100px;
        width: 90px;
        border-radius: 15px;
    }
    .listingimage{
      padding: 0;
    }
    .secondcontainer{
        margin:0 0 10px 0;
        width: 100%;
        padding: 5px;
    }
    .secondpage{
        height:100%;
        overflow: hidden;
    }
    .heading{
        font-size: 18px;
    }
    .restdetail{
        padding: 0px 0 0 8px;
        font-size: 10px;
        font-weight: 400;
    }
    .image{
        padding:0px;
    }
    .fontsize{
        font-size: 13px;
        font-weight: 600;
    }
    .restName{
        font-size: 13px;
        margin: 0;
    }
    .restprice{
        font-size: 10px;
    }
}
